import React, { useEffect, useMemo, useState } from "react";
import { Container, Grid, Paper } from "@mui/material";
import CabinetTop from "../../../components/cabinetTop";
import CryptoBlock, { DisabledCard } from "../../../components/CryptoBlock";
import Balance from "../../../components/Balance";
import CryptoSkeleton from "../../../components/CryptoSkeleton";
import { useDispatch, useSelector } from "react-redux";
import { getСurrenciesList } from "../../../redux/reducers/Сurrencies";

const Buy = ({ t, setVerificationPush, mode }) => {
  const [filter, setFilter] = useState("");

  const dispatch = useDispatch();

  const { СurrenciesList, СurrenciesLoading } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );

  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const account = profileData?.data?.profile?.verification?.value;

  useEffect(() => {
    dispatch(getСurrenciesList());
    // eslint-disable-next-line
  }, []);

  const filteredCoin = useMemo(
    () =>
      СurrenciesList.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const Сurrencies = filter ? filteredCoin : СurrenciesList;
  return (
    <Paper component="section">
      <Container>
        <Balance />
        <CabinetTop title={t("buyEMoney")} t={t} input setFilter={setFilter} />
        <Grid container spacing={2}>
          {!СurrenciesLoading
            ? Array.from(Array(12).keys()).map((item, idx) => (
                <Grid item xs={12} key={idx} sm={6} md={3} lg={3} xl={3}>
                  <CryptoSkeleton style={{ width: 389 }} />
                </Grid>
              ))
            : Сurrencies
                .filter((item) => item.can_buy)
                .map((item, idx) => (
                  <Grid
                    key={idx}
                    item
                    xs={12}
                    sm={6}
                    md={3}
                    lg={3}
                    xl={3}
                    onClick={() =>
                      localStorage.setItem("sellDetail", JSON.stringify(item))
                    }
                  >
                    {account == 2 ? (
                      <CryptoBlock
                        mode={mode}
                        t={t}
                        setVerificationPush={setVerificationPush}
                        item={item}
                        href={`/dashboard/buy/details/${item.currency}`}
                      />
                    ) : (
                      <DisabledCard
                        mode={mode}
                        t={t}
                        setVerificationPush={setVerificationPush}
                        item={item}
                      />
                    )}
                  </Grid>
                ))}
        </Grid>
      </Container>
    </Paper>
  );
};

export default Buy;
